.content {
    @include flex-col();
    align-items: center;
    font-weight: 300;
    margin: 94px 0;

    .container {
        @include flex-row();
        width: 800px;
        height: 100%;

        &>* {
            width: 50%;
            margin-top: 1px;
        }
    }
}


